| Type_Array(ixtype,ty) -> group(string "" ^^ string "array" ^^ break 1 ^^ string "[" ^^ break 1 ^^ nest 2 (pp_ixtype ixtype) ^^ break 1 ^^ string "]" ^^ break 1 ^^ string "of" ^^ break 1 ^^ nest 2 (pp_ty ty) ^^ string "")
