and pp_regfield rf = separate (string "\n") (List.map pp_slice (fst rf)) ^^ string " " ^^ pp_ident (snd rf)

and pp_stmt x = match x with
| Stmt_VarDeclsNoInit(ty,ident_list,ott_menhir_loc) -> group(string "" ^^ nest 2 (pp_ty ty) ^^ break 1 ^^ nest 2 (pp_ident_list ident_list) ^^ break 1 ^^ string ";" ^^ string "")
